<template>
	<div class="container-fluid">
		<h1>Додати нову інформацію</h1>
		<form @submit.prevent="addInfo(elem)">
			<div class="row">
				<div class="col-12">
					<div class="form-group">
					    <label for="type">Тип форми</label>
					    <select id="type" v-model='elem.type'>
					    	<option value="text">Text</option>
					    	<option value="email">Email</option>
					    	<option value="tel">Phone</option>
					    	<option value="textarea">Textarea</option>
					    	<option value="image">Image</option>
					    </select>
					</div>
					<div class="form-group">
					    <label for="title">Заголовок:</label>
					    <input type="text" v-model='elem.title' class="form-control" id="title">
					</div>
					<div class="form-group">
					    <label for="value">Значення</label>
					    <preview-image  v-if='elem.type === "image"'/>
					    <textarea v-else v-model='elem.value' class="form-control" id="value"></textarea>
					</div>
					<div class="form-group">
					    <label for="alias">Аліас</label>
					    <input type="text" v-model='elem.alias' class="form-control" id="alias">
					</div>
					
				</div>
				<!-- /.col-12 col-lg-6 -->								
			</div>
			<button class="btn btn-primary" type="submit">Додати інформацію</button>
		</form>
	</div>
	
</template>
<script>
	const PreviewImage = () => import('@/components/PreviewImage');

	export default {
		name: 'addPersonalInfo',
		data() {
			return {
				elem: {
					title: '',
					type: '',
					value: '',
					alias: ''
				}
			}
		},
		methods: {
			addInfo(item) {
				const createdAt = new Date();
				this.$_db.collection('personal').add({createdAt, ...item })
			}
		},
		components: {
			PreviewImage
		}
	}
</script>